import './style.scss';
import React from 'react';
import {Helmet} from "react-helmet";
import scrollAnimate from "../../libs/scrollAnimate";
import Navigation from "../../components/navigation/navigation";
import HeaderHome from "./headerHome";
import FeaturePlatformHome from "./featurePlatformHome";
import FeatureList from "./featureList";
import CustomerHome from "./customerHome";
import FeedbackHome from "./feedbackHome";
import ReasonHome from "./reasonHome";
import RegisterHome from "./registerHome";
import Guarantee from "./guaranteeHome";
import Footer from "../../components/footer/footer";
import ScrollToTopOnMount from "../../components/ScrollToTopOnMount";
import { Translation } from 'react-i18next';
import MasterComponent from "../../components/master";

class Home extends MasterComponent {

    componentDidMount() {
        scrollAnimate();
    }

    render() {
        return (
            <React.Fragment>
                <Translation>
                    {(t) => (
                        <Helmet>
                            <title>{t("home:title")}</title>
                            <meta name="description" content={t("home:metaDescription")}/>
                            <meta name="keywords" content={t("home:metaKeyWord")}/>
                        </Helmet>
                    )}
                </Translation>
                <ScrollToTopOnMount/>
                <Navigation props={this.props}/>
                <div id="home" className="animated">
                    <HeaderHome/>
                    <FeaturePlatformHome/>
                    <FeatureList/>
                    <CustomerHome/>
                    <FeedbackHome/>
                    <ReasonHome/>
                    <RegisterHome/>
                    <Guarantee/>
                </div>
                <Footer/>
            </React.Fragment>
        )
    }
}

export default Home;
